import request from '@/utils/request'

// 订单结算确认
// mode :cart => obj { cartId }
// mode :buyNow => obj { goodsId goodsNum goodsSkuId }
export const checkOrder = (mode, obj) => {
  return request.get('/checkout/order', {
    params: {
      mode, // cart  buynow
      delivery: 20, // 10快递  20门店自提
      coutonId: 0, // 优惠券id   不使用优惠卷
      isUsePoints: 0, // 积分传0 表示不使用积分
      ...obj // 将传递过来的参数对象，动态展开
    }
  })
}
// 提交订单
// mode :cart => obj { cartId,remark }
// mode :buyNow => obj { goodsId goodsNum goodsSkuId,remark }
export const submintOrder = (mode, obj) => {
  return request.post('/checkout/submit', {
    mode,
    delivery: 20, // 10快递配送
    coutonId: 0,
    isUsePoints: 0,
    payType: 10, // 余额支付
    ...obj
  })
}
export const getMyOrderList = (dataType, page) => {
  return request.get('/order/list', {
    params: {
      dataType,
      page
    }
  })
}
